
.iban-container {
  background-image: url('/public/images/viaggiodinozze.jpg');
  background-size: cover;
  background-position: center;
  padding: 3rem 2rem;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.iban-content {
  background-color: rgba(255, 255, 255, 0.85);
  padding: 2rem;
  border-radius: 1rem;
  max-width: 600px;
  width: 100%;
  text-align: center;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  
}

.iban-content h2 {
  font-family: 'Tangerine', cursive;
  font-size: 3rem;
  margin-bottom: 1rem;
  color: #e9b948;
}

.iban-content p {
  font-family: 'Lora', serif;
  font-size: 1.1rem;
  margin-bottom: 1rem;
  color: #34495e;
}

.home-button {
  margin-top: 1.5rem;
  font-family: 'Tangerine', cursive;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  background-color: #e08b2f;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.home-button:hover {
  background-color: #1a242f;
}

/* Mobile first */
@media (max-width: 600px) {
  .iban-content {
    padding: 1.5rem;
  }

  .iban-content h2 {
    font-size: 2rem;
  }

  .iban-content p {
    margin-top: 10px;
    font-size: 1rem;
  }

    .home-button {
    font-family: 'Tangerine', cursive;
    width: 60%;
    font-size: 1.2rem;
    background-color: #e08b2f;
  }
}
