.menu-container {
  background-image: url('/public/images/allergieintolleranze.jpg');
  background-color: #f5fdf6;
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
  min-height: 100vh;
  padding: 4rem 2rem;
  font-family: 'Tangerine', cursive;
  color: #2f4f4f;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  max-width: 1200px;
  margin: 0 auto;
}

.menu-content {
  max-width: 700px;
  width: 100%;
  margin: 0 auto;
  text-align: center;
}

.menu-container h2 {
  font-size: 3rem;
  margin-top: 4rem;
  margin-bottom: 2rem;
  color: #385b4f;
}

.menu-container h3 {
  font-size: 1.3rem;
  margin-bottom: 2rem;
  color: #385b4f;
}

.menu-container ul {
  list-style: none;
  padding: 20;
  margin: 0 auto 5rem;
  max-width: 500px;
}

.menu-container li {
  margin: 1.2rem 0;
  font-size: 1.5rem;
  line-height: 1.4;
  display: flex;
  flex-direction: column; /* fondamentale per input e bottone sotto label */
  align-items: flex-start;
  gap: 0.5rem;
  padding-left: 1.5rem; /* sposta leggermente a sinistra rispetto al centro */
}

.menu-container li label {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 1.5rem;
  cursor: pointer;
  flex-wrap: wrap;
}

.menu-container input[type="checkbox"] {
  width: 24px;
  height: 24px;
  cursor: pointer;
}

.menu-container input[type="text"] {
  padding: 0.5rem 0.75rem;
  font-size: 1.2rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-family: inherit;
  width: 250px;
  box-sizing: border-box;
  margin-top: 0.5rem;
}

/* BUTTON SEGNALA */
.segnala-btn {
  margin-top: 3px;
  margin-left: 10px;
  background-color: #b66c13;
  color: white;
  border: none;
  padding: 0.6rem 1.2rem;
  font-size: 1.2rem;
  font-family: 'Tangerine', cursive;
  cursor: pointer;
  border-radius: 6px;
  transition: background-color 0.3s ease;
  align-self: flex-start; /* lo allinea con il testo, non al centro della pagina */
}

.segnala-btn:hover {
  background-color: #e08b2f;
}

/* BUTTON TORNA ALLA HOME */
.back-home-btn {
  margin-top: 4rem;
  background-color: #b66c13;
  color: white;
  border: none;
  padding: 1rem 2rem;
  font-size: 1.5rem;
  font-family: 'Tangerine', cursive;
  cursor: pointer;
  border-radius: 8px;
  transition: background-color 0.3s ease;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.back-home-btn:hover {
  background-color: #e08b2f;
}

/* MOBILE */
@media (max-width: 768px) {
  .menu-container {
    padding: 1rem 0.5rem;
    background-size: 95%;
    align-items: center;
  }

  .menu-content {
    text-align: center;
    margin: 0 auto;
    width: 100%;
    max-width: 360px;
  }

  .menu-container h2 {
    font-size: 2.4rem;
    text-align: center;
    margin-bottom: 0.8rem;
  }

  .menu-container h3 {
    font-size: 1.2rem;
    margin-bottom: 1rem;
    text-align: center;
  }

  .menu-container ul {
    padding: 0;
    margin: 0 auto 1.5rem;
    max-width: 100%;
  }

.menu-container li {
  font-size: 1.2rem;
  margin: 0.8rem 0;
  display: flex;
  align-items: center;
  gap: 0.6rem;
  justify-content: center;
}

.menu-container li label {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  justify-content: flex-start;  /* << appena piÃ¹ a sinistra */
  width: 100%;
  max-width: 270px;
  margin: 0 auto;
  padding-left: 8px;  /* lieve spinta verso sinistra */
}


.menu-container input[type="text"] {
  width: 90%;
  max-width: 200px;
  font-size: 0.9rem;
  padding: 0.35rem 0.5rem;
  margin-top: 0.1rem;
  display: block;
  margin-left: auto;
  margin-right: 40px;
}


  .segnala-btn {
    font-size: 1rem;
    padding: 0.5rem 0.1rem;
    margin-top: 1rem;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }

  .back-home-btn {
    font-size: 1.1rem;
    padding: 0.6rem 1.2rem;
    margin-top: 2rem;
  }
}