.home-container {
  background-image: url('/public/images/home-background2.jpg'); 
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
  height: 80vh;
  max-height: 600px;
  width: 100vw;
  position: relative;
  font-family: 'Tangerine', cursive;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-top: 2rem;
}


.hamburger-btn {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 22px;
  background: transparent;
  border: none;
  cursor: pointer;
  position: absolute;
  top: 20px;
  left: 60%;             
  transform: translateX(0);  
  z-index: 10;
}

.hamburger-btn .bar {
  height: 3px;
  width: 100%;
  background-color: #937e03;
  border-radius: 2px;
}


.top-menu {
  display: none;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.9);
  position: absolute;
  top: 40px;          
  left: 60%;          
  transform: translateX(0);
  padding: 1rem;
  border-radius: 8px;
  box-shadow: 0 5px 10px rgba(0,0,0,0.15);
  width: 180px;
  font-size: 1.2rem;
  font-family: 'Tangerine', cursive;
  z-index: 5;
}


.top-menu.open {
  display: flex;
}


.top-menu a {
  padding: 0.5rem 0;
  color: #b66c13;
  text-decoration: none;
  font-style: italic;
  transition: color 0.3s ease;
}

.top-menu a:hover {
  color: #e08b2f;
}


@media (max-width: 768px) {
  .home-container {
    background-size: cover;
    background-position: center top;
    height: 100dvh;
    max-height: none;
  }

  .hamburger-btn {
    left: auto;
    margin-top: 15px;
    right: 20px;
    transform: none;
  }

  .top-menu {
    left: auto;
    right: 20px;
    transform: none;
    top: 40px;
    width: 180px;
  }
}
